body {
  font-weight: 500; }

h2 {
  margin: 0.9em 0;
  font-size: 18px;
  font-weight: bold; }

h3 {
  margin: 0;
  font-size: 24px;
  font-weight: bold; }

.collection__container {
  border-radius: 16px;
  background-color: #f2f2f2;
  padding: 24px;
  max-width: 600px;
  color: #191a1b;
  font-size: 14px; }

.collection__coins {
  margin: 16px 0; }

.collection__coin {
  margin-bottom: 8px;
  font-weight: bold; }

.collection__ticker {
  display: block;
  margin-right: 6px; }

.collection__address {
  color: #c883ff;
  font-size: 12px;
  font-weight: normal; }

.collection__subtitle {
  margin: 40px 0; }

@media only screen and (min-width: 568px) {
  .collection__container, .collection__address {
    font-size: 18px; }
  .collection__ticker {
    display: inline; }
  .collection__coin {
    margin-bottom: 0; } }



