body {
  font-weight: 500; }

h2 {
  margin: 0.9em 0;
  font-size: 18px;
  font-weight: bold; }

h3 {
  margin: 0;
  font-size: 24px;
  font-weight: bold; }

.header__logo {
  position: absolute;
  top: 24px;
  left: 24px;
  width: 101px;
  height: 35px; }

.header__background {
  margin-bottom: -25%;
  margin-left: 30%;
  width: 70%; }

.header__intro {
  font-size: 18px; }

.header__title {
  margin-top: 60px; }

@media only screen and (min-width: 321px) {
  .header__intro {
    font-size: 24px; } }

@media only screen and (min-width: 768px) {
  .header__container {
    max-width: 520px; }
  .header__logo {
    position: relative; }
  .header__background {
    position: absolute;
    top: 0;
    right: 0;
    margin-bottom: 0;
    margin-left: 0;
    width: 45%;
    max-width: 680px; }
  .header__title {
    margin-top: 170px;
    margin-bottom: 0;
    font-size: 3em; } }



